import React from 'react'
import { Calendar, Confirmation, Info, Method} from './fields'

interface Props{
    step: number
}

export default class Page extends React.Component<Props>{
    static getInitialProps(){
        const step = 1
        return { step }
    }

    render(){
        const { step } = this.props
        switch(step){
            case 1:
                return(<Info/>)
            case 2:
                return(<Calendar/>)
            case 3:
                return(<Method/>)
            case 4:
                return(<Confirmation/>)
        }
    }
}